body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.uk-navbar-container{
  background-color: #4a2748 !important;
  color:white;
}

.nav-title{
  color:white !important;
}

.container-left{
  width:100%
}

.container-right{
  width:100%;
}

.emi-tenure{
  padding-top:8%;
}

.uk-card1{
    margin-left: auto;
    margin-right: auto;
    margin-top:1%;
    width:50%;
}

.uk-grid-divider{
  width: 84%;
  margin-left: auto;
  margin-right: auto;
}

@media only screen and (max-width: 800px){
  .container-left{
    width:100%
  }
  
  .container-right{
    width:100%;
  }
  .uk-card1{
    width:100%;
}
.uk-grid-divider{
  width: 100%;
}

.uk-card2{
  padding:0px !important;

}
}

.input-range__slider{
  background: #4a2748 !important;
  border: 1px solid #4a2748 !important;
}

.input-range__track--active {
  background: #4a2748 !important;
}



